function PendingDialog(props) {

    const { open, item, onOk, onCancel } = props
    const [employee, setEmployee] = useState()
    const columns = Columns()
    const [shouldOpenPendingDialogConfirm, setShouldOpenPendingDialogConfirm] = useState(false)
    const [titlePendingDialogConfirm, setTitlePendingDialogConfirm] = useState("")

    const showPendingDialogConfirmRefuse = () => {
        setTitlePendingDialogConfirm("Từ chối phê duyệt")
        setShouldOpenPendingDialogConfirm(true)
    }

    const showPendingDialogConfirmApprove = () => {
        setTitlePendingDialogConfirm("Xác nhận phê duyệt")
        setShouldOpenPendingDialogConfirm(true)
    }

    const showPendingDialogConfirmAdditional = () => {
        setTitlePendingDialogConfirm("Yêu cầu bổ sung")
        setShouldOpenPendingDialogConfirm(true)
    }

    const hidenPendingDialogConfirm = () => {
        setTitlePendingDialogConfirm("")
        setShouldOpenPendingDialogConfirm(false)
    }

    const okPendingDialogConfirm = async (date, comment, eligible) => {
        switch (titlePendingDialogConfirm) {
            case "Từ chối phê duyệt":
                if (date === "" || comment === "") {
                    alert("Nhập đủ các trường")
                    return
                }

                await (rejectEmployee({
                    employeeId: item.employeeId,
                    status: 6,
                    rejectedReason: comment
                }))

                break
            case "Xác nhận phê duyệt":
                if (date === "" || eligible === false) {
                    alert("Nhập đủ các trường")
                    return
                }

                const resApprove = await (approveEmployee({
                    employeeId: item.employeeId,
                    status: 5
                }))

                break
            case "Yêu cầu bổ sung":
                if (comment === "") {
                    alert("Nhập đủ các trường")
                    return
                }

                console.log("Bổ sung: ", comment)
                const resRequi = await (requiredSupplementEmployee({
                    employeeId: item.employeeId,
                    status: 4,
                    statusLog: comment
                }))
                console.log(resRequi)

                break
            default:
                console.log("Loi dau do")
                break
        }
        hidenPendingDialogConfirm()
        onCancel()
    }

    useEffect(() => {
        const getInfo = async () => {
            const res = await getById(item.employeeId)
            setEmployee(res.data.data)
        }
        getInfo()
    }, [])

    return (
        <div>
            {shouldOpenPendingDialogConfirm && (
                <PendingDialogConfirm
                    open={shouldOpenPendingDialogConfirm}
                    title={titlePendingDialogConfirm}
                    onCancel={hidenPendingDialogConfirm}
                    onOk={okPendingDialogConfirm}
                />
            )}

            <StyledModal
                centered
                title="Hồ sơ nhân viên"
                width={1000}
                open={open}
                onOk={onOk}
                onCancel={onCancel}
                footer={
                    <div style={{ display: "flex", justifyContent: "center", gap: "0 10px" }}>
                        <Button key="cancel" type="primary" onClick={onCancel}
                            style={{ backgroundColor: "orange", color: "white", margin: 0 }}
                        >
                            Hủy
                        </Button>
                        <Button key="notAgreed" type="primary" danger onClick={showPendingDialogConfirmRefuse}
                            style={{ margin: 0 }}
                        >
                            Từ Chối
                        </Button>
                        <Button key="accept" type="primary" onClick={showPendingDialogConfirmApprove}
                            style={{ backgroundColor: "green", color: "white", margin: 0 }}
                        >
                            Duyệt
                        </Button>
                        <Button key="submit" type="primary" onClick={showPendingDialogConfirmAdditional}
                            style={{ backgroundColor: "blue", color: "white", margin: 0 }}
                        >
                            Yêu cầu bổ sung
                        </Button>
                    </div>
                }
            >
                <div>
                    <Tabs
                        defaultActiveKey="1"
                        tabPosition="left"
                        style={{
                            height: 600,
                        }}
                        tabBarStyle={{ width: 200, whiteSpace: "break-spaces" }}
                        items={[
                            {
                                label: "HỒ SƠ",
                                key: 1,
                                children: <RenderBrief data={employee} />
                            },
                            {
                                label: "SƠ YẾU LÝ LỊCH",
                                key: 2,
                                children: <RenderCurriculumVitae data={employee} />
                            },
                            {
                                label: "DANH SÁCH VĂN BẰNG",
                                key: 3,
                                children: <RenderDegrees columns={columns} data={employee} />
                            },
                        ]}
                    />
                </div>
            </StyledModal>

        </div>
    )
}

export default PendingDialog

const StyledModal = styled(Modal)`
    .ant-modal-content{
        overflow: hidden;
        height: 90vh;
    }
    .ant-modal-body{
        min-height: 76vh;
        overflow: hidden;
    }

    .ant-modal-footer{
        position: absolute;
        bottom: 0;

    }
`;
